import "core-js/modules/es.object.to-string.js";
import axios from 'axios'; // 共通のヘッダーを設定したaxiosのインスタンス作成

var gasApi = axios.create({
  headers: {
    'content-type': 'text/plain'
  }
}); // response共通処理
// errorが含まれていたらrejectする

gasApi.interceptors.response.use(function (res) {
  if (res.data.error) {
    return Promise.reject(res.data.error);
  }

  return Promise.resolve(res);
}, function (err) {
  return Promise.reject(err);
});
/**
 * APIのURLを設定します
 * @param {String} url
 */

var apiUrl = '';

var setUrl = function setUrl(url) {
  apiUrl = url;
};
/**
 * authTokenを設定します
 * @param {String} token
 */


var authToken = '';

var setAuthToken = function setAuthToken(token) {
  authToken = token;
};
/**
 * 指定年月のデータを取得します
 * @param {String} yearMonth
 * @returns {Promise}
 */


var fetch = function fetch(yearMonth) {
  return gasApi.post(apiUrl, {
    method: 'GET',
    authToken: authToken,
    params: {
      yearMonth: yearMonth
    }
  });
};
/**
 * データを追加します
 * @param {Object} item
 * @returns {Promise}
 */


var add = function add(item) {
  return gasApi.post('', {
    method: 'POST',
    authToken: authToken,
    params: {
      item: item
    }
  });
};
/**
 * 指定年月&idのデータを削除します
 * @param {String} yearMonth
 * @param {String} id
 * @returns {Promise}
 */


var $delete = function $delete(yearMonth, id) {
  return gasApi.post('', {
    method: 'DELETE',
    authToken: authToken,
    params: {
      yearMonth: yearMonth,
      id: id
    }
  });
};
/**
 * データを更新します
 * @param {String} beforeYM
 * @param {Object} item
 * @returns {Promise}
 */


var update = function update(beforeYM, item) {
  return gasApi.post('', {
    method: 'PUT',
    authToken: authToken,
    params: {
      beforeYM: beforeYM,
      item: item
    }
  });
};

export default {
  setUrl: setUrl,
  setAuthToken: setAuthToken,
  fetch: fetch,
  add: add,
  delete: $delete,
  update: update
};