import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import Settings from '../views/Settings.vue';
Vue.use(VueRouter);
var routes = [{
  path: '/',
  name: 'Home',
  component: Home
}, {
  path: '/settings',
  name: 'Settings',
  component: Settings
}];
var router = new VueRouter({
  routes: routes
});
export default router;